body {
  margin: 0 auto;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  min-width: 320px;
}
.body__sprite {
  display: none;
}
.body__wrapper {
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-position: top center;
  background-repeat: no-repeat;
  background-size: 100% 465px;
  background-color: #1d2631;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .body__wrapper {
    background-image: url("../img/bg-back-mobile@2x.jpg");
  }
}
.body--opened .body__wrapper,
.body--nojs .body__wrapper {
  background-position: top 257px center;
}
a {
  text-decoration: none;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 660px) {
  body {
    min-width: 660px;
  }
  .body__wrapper {
    background-image: url("../img/bg-back-tablet@1x.jpg");
    background-position: top center;
    background-size: 182%;
