.header {
  display: flex;
  position: relative;
  text-transform: uppercase;
}
.header__image {
  position: absolute;
  top: 21px;
  left: 21px;
  z-index: 9;
  fill: #ffffff;
}
.header__image--tablet {
  display: none;
}
.header__image--desktop {
  display: none;
}
.header__logo:hover .header__image,
.header__logo:focus .header__image {
  fill: #d22856;
}
.header__logo:active .header__image {
  fill: #ffffff;
  opacity: 0.3;
}
@media (min-width: 660px) {
  .header__image {
    display: none;
  }
  .header__image--tablet {
    display: block;
    position: absolute;
    top: 27px;
    left: 20px;
    z-index: 9;
  }
  .header__image--desktop {
    display: none;
  }
}
@media (min-width: 960px) {
  .header__image--desktop {
    display: block;
    position: absolute;
    top: 27px;
    left: 10px;
    z-index: 9;
  }
  .header__image--tablet {
    display: none;
  }
  .header__logo:hover .header__image,
  .header__logo:focus .header__image {
    fill: #d22856;
  }
  .header__logo:active .header__image {
    fill: #ffffff;
    opacity: 0.3;
  }
  .header {
    text-transform: none;
    width: 960px;
    margin: 0 auto;
  }
}
.nav {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  min-height: 70px;
  width: 100%;
  background-color: #283645;
}
.nav__toggle {
  height: 23px;
  width: 25px;
  background-image: url("../img/icon-menu-cross.svg");
