.nav {
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  min-height: 70px;
  width: 100%;

  background-color: @color-main;
}

.nav__toggle {
  height: 23px;
  width: 25px;

  background-image: url("../img/icon-menu-cross.svg");
  background-color: @color-main;
  background-repeat: no-repeat;

  margin-top: 20px;
  margin-right: 18px;

  border: 0;

  &:hover,
  &:focus {
    background-image: url("../img/icon-menu-cross-hover.svg");
  }

  &:active {
    opacity: 0.3;
  }
}

@media (max-width: @width-menu) {
  .body--closed .nav {
    background-color: rgba(0, 0, 0, 0.3);
  }

  .body--nojs .nav {
    background-color: @color-main;
  }

  .body--closed .nav__toggle {
    background-image: url("../img/icon-menu-burger.svg");
    height: 24px;
    width: 51px;

    &:hover,
    &:focus {
      background-image: url("../img/icon-menu-burger-hover.svg");
    }

    &:active {
      opacity: 0.3;
    }
  }

  .body--nojs .nav__toggle {
    display: none;
  }
}

@media (min-width: @width-tablet) {
  .nav {
    min-height: 110px;
  }

  .nav__toggle {
    margin-top: 45px;
  }
}

@media (min-width: @width-desktop) {
  .nav {
    background-color: rgba(0, 0, 0, 0);
  }

  .nav__toggle {
    display: none;
  }
}
